import React from "react";
import style from "./BordsRightStyle.module.css";

const BordsRight = () => {
  return (
    <div className={style.leftRig}>
      <div className={style.rig}>
        <img src="./img/rig.jpg" alt=" " />
      </div>
    </div>
  );
};
export default BordsRight;
